  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0px 60px 15px 60px;
  border: 3px;
  background-color: #e9e8ef;
}

.app-container > h1 {
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.app-container > hr {
